#header{
  background-image: url("bjk.jpg");
  background-size: cover;    
  width: 100%;
  height: 90vh;
  position: relative;
}
.header-txt{
position: absolute;
top:30%;
left:2%;

font-family: 'Times New Roman', Times, serif;
padding: 10px;
color: rgb(63, 62, 62);


}
.header-pri{
  font-size: 40px;
  font-weight: 700;
  letter-spacing: 1.3px;
}
.header-sec{
  font-size: 30px;
  font-weight: 500;
  letter-spacing: 1.2px;
}

.header-btn{
  padding: 10px;
  font-size: 20px;
  text-decoration: none;
  background-color: black;
  color: white;
  font-weight: 500;
  transition: all .5s;
  box-shadow: 2px 4px 6px rgb(105, 104, 104);
  cursor: pointer;

}
.header-btn:hover{
  transform: scale(1.2);
  box-shadow: 4px 6px 8px rgb(72, 72, 72);

}